    display: block;
    width: 100%;
    max-width: 300px; /* Max width for large devices */
    padding: 8px 12px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 20px;
    background-color: #fff;
    transition: all 0.3s ease;
  }
  
  /* Responsive styles for different screen sizes */
  
  /* For mobile screens */
  @media (max-width: 640px) {
    .search-input {
      max-width: 80%; /* Take up 80% of the width on mobile */
    }
  }
  
  /* For tablets and small laptops */
  @media (min-width: 641px) and (max-width: 1024px) {
    .search-input {
      max-width: 60%; /* Take up 60% of the width */
    }
  }
  
  /* For larger devices like laptops and desktops */
  @media (min-width: 1025px) {
    .search-input {
      max-width: 300px; /* Limit width to 300px */
    }
